.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
}

.modal-content {
    position: relative;
    background: #fff;
    padding: 20px;
    border-radius: 8px;
    max-width: 900px;
    width: 100%;
    overflow-y: auto;
    max-height: 80vh;
}

.modal-close {
    background: none;
    border: none;
    font-size: 1.5rem;
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
}

table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 10px;
}

th, td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
}

th {
    background-color: #f4f4f4;
}

.status-paid {
    color: green;
}

.status-canceled {
    color: red;
}

.total-salary{
    width: 100%;
    display: flex;
    justify-content: end;
    margin-top: 30px;
}

.total-head{
    font-size: 30px;
    font-weight: 400;
}

.total-cost{
    font-size: 30px;
    font-weight: 600;
}

@media (max-width:600px) {
    .total-salary{
        justify-content: start;
    }
    .total-head{
        font-size: 15px;
        font-weight: 400;
    }
    
    .total-cost{
        font-size: 15px;
        font-weight: 600;
    }
}